.app {
    padding: 60px;
    background-color: #000;
    &__wrapper { 
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__container {
        margin-top: 20px;

        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__start {
        margin: 100px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__step {
        width: 136px;
        height: 54px;
        border-radius: 145.746px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #262626;
        &--container{
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: -30px;
        }
    }

    &__step--text {
        font-size: 15px;
    }

    &__whatdo {
        max-width: 418px;
        margin-top: 20px;

    }

    &__whatdo--text { 
        text-align: center;
        font-size: 82px;
        line-height: 82px;
        letter-spacing: 4.55457px;
    }

    &__description {
        max-width: 230px;
        margin: 80px auto;
        text-align: center;
        &--container{
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__description--text {
        margin: 0 auto;
        font-size: 16px;
        line-height: 29px;
        letter-spacing: 0.728731px;
    }

    &__image {
        background-color: pink;
        border-radius: 30px;
        width: 400px;
        height: 580px;
        &--card{
            background: url('../img/golden-house.webp') no-repeat,
             url('../img/card.webp') no-repeat,
             url('../img/golden-house.webp') no-repeat;
            background-position: 0 -20px, 60% 150px,170px 300px;
            background-clip: cover;
            background-size: 50%,60%,70%;
            width: 100%;
            height: 100%;
            transform: rotate(-15deg);
        }
        &--makebank{
            background: url('../img/makebank-img.webp');
            background-position: center;
            background-clip: cover;
            width: 100%;
            height: 100%;
        }
        &--cashout{
            background: url('../img/cashout.webp');
            background-position: center;
            background-clip: cover;
            width: 100%;
            height: 100%;
        }
    }
}
