.cards {
    background-color: #000;
    width: 100%;
    height: max-content;
    padding: 10px;
    &__container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap-reverse;
        gap: 10px;
        flex: 0 1 45%;
        margin: 100px 0;
    }

    &__card {
        margin-top: 150px;
        width: 501px;     
        border: 2px solid #262626;
        border-radius: 25.5056px;
    }

    &__earn-card {
        background-color: #000;
    }
    &__money-card{
        background-color: #fff;
        color: #000;
        position: relative;
    }

    &__money-card .card__section{
        border-color: #f6f6f6;
    }
} 
.card-money__card{
    top: -17%;
    left: 8%;
    width: 412px;
    height: 317px;
    border-radius: 20px;
    position: absolute;
    background: url('../img/card.webp');
}
.card {
   
    &__header {
        padding-top: 100px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &__title {
        text-align: center;
    }

    &__title--text {
        padding: 23px;
        font-size: 82px;
        line-height: 82px;
    }

    &__change {
        text-align: center;
    }

    &__change--text {
        line-height: 24px;
        letter-spacing: 0.437238px;
    }

    &__way {
        text-align: center;
        padding: 20px;
    }

    &__way--text {
        font-size: 31px;
        line-height: 49px;  
    }

    &__section {
        border-top: 2px solid #262626;
    }

    &__section--container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 36px 24px;
    }

    &__text {
        
    }

    &__text--text {
        font-size: 1rem;
        line-height: 24px;
        letter-spacing: 0.437238px;
    }

    &__have {
        background-color: #FFC8D0;
        background-image: url('../img/cheched.webp');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 30%;
        width: 55px;
        height: 55px;
        border-radius: 50%;
        &--not-checked{
            width: 55px;
            height: 55px;
            border-radius: 50%;
            background-color: transparent;
            border: 2px solid rgba(255, 255, 255, 0.2);
        }
    }

    &__price {
        padding: 15px 41px;
        border-radius: 218.619px;
        background: #FFC8D0;
    }

    &__price--text {
        color: #000;
    }
    &__button{
        
        width: 100%;
        padding: 25px;
        
        &--check{
            height: 75px;
            width: 100%;
            border-radius: 13.6637px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #FFC8D0;
            cursor: pointer;
        }
    }
    &__check--text{
        color: #000;
        font-size: 1rem;
        line-height: 20px;
        letter-spacing: 0.437238px;
        position: relative;
        &::after{
            content: "";
            position: absolute;
            top: 6px;
            right: -33px;
            width: 20px;
            height: 20px;
            background-image: url('../img/arrow.png');
            background-repeat: no-repeat;
        }
    }
}
